<template>
  <card class="live-orders-card">
    <div class="live-orders-card_title">
      <div class="live-orders-card_title-price">Price</div>
      <div class="live-orders-card_title-amount">Amount</div>
      <div class="live-orders-card_title-time">Time</div>
    </div>
    <div class="live-orders-card_all">
      <div
        class="live-orders-card_all_order"
        v-for="order in [...orders].reverse()"
        :key="hash(order)"
      >
        <div :class="['live-orders-card_all_order-price', order.action]">
          {{ order.price }}
        </div>
        <div class="live-orders-card_all_order-amount">{{ order.amount }}</div>
        <div class="live-orders-card_all_order-time">{{ order.time }}</div>
      </div>
    </div>
    <div class="divider"></div>
  </card>
</template>

<script>
import Card from '../Card'

export default {
  name: 'live-orders-card',
  components: { Card },
  inject: ['rates'],
  data: () => ({
    orders: [],
    symbol: 'BTC',
  }),
  methods: {
    hash: require('object-hash'),
    addOrder() {
      if (this.$route.name != 'Trading') return

      var price = this.rates[this.symbol]?.price
      var amount = (Math.random() * Math.random()) / 10
      var t = new Date()

      if (price) {
        if (this.orders.length > 100) this.orders.shift()

        if (Math.random() > 0.9) amount += 1
        else if (Math.random() > 0.9) amount += 0.1

        this.orders.push({
          price,
          amount: amount.toFixed(10),
          time:
            (t.getHours() < 10 ? '0' + t.getHours() : t.getHours()) +
            ':' +
            (t.getMinutes() < 10 ? '0' + t.getMinutes() : t.getMinutes()) +
            ':' +
            (t.getSeconds() < 10 ? '0' + t.getSeconds() : t.getSeconds()),
          action: Math.random() > 0.5 ? 'buy' : 'sell',
        })
      }

      setTimeout(
        this.addOrder,
        Math.random() * 300 + 100 + (this.$isDesktop ? 0 : 1500),
      )
    },
    changeCurrency(net) {
      this.symbol = net
    },
  },
  mounted() {
    this.$root.$on('change-currency', this.changeCurrency)

    this.addOrder()
  },
  destroyed() {
    this.$root.$off('change-currency', this.changeCurrency)
  },
}
</script>

<style lang="scss" scoped>
@import '../../../../scss/smart-grid';
@import '../../../../scss/variables';

.live-orders-card {
  grid-area: live-orders;
  flex-direction: column;

  @include from(100rem) {
    display: flex;
  }

  @include to(100rem) {
    display: none;
  }

  &_title {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    padding-right: 40px;
    color: $light-blue;
    user-select: none;
  }

  &_all {
    width: 100%;
    height: 100%;
    overflow: auto;
    &_order {
      padding-right: 10px;
      margin: 5px;
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      color: $cyan;

      &:hover {
        background-color: $cyan-alpha;
      }

      &-price {
        &.buy {
          color: #02c076;
        }

        &.sell {
          color: $red;
        }
      }
    }
  }
}
</style>
