<template>
  <div class="currency-selector">
    <div
      @click="
        () => {
          current = currency
        }
      "
      v-for="currency in currencies"
      :class="['currency-selector_item', current == currency ? 'active' : '']"
      :key="currency"
    >
      <Icon :icon="['currency__icon', currency]" />
    </div>
  </div>
</template>

<script>
import Icon from '@/components/partial/Icon.vue'

export default {
  name: 'currency-selector',
  components: { Icon },
  data: () => ({
    current: 'bitcoin',
    currencies: ['bitcoin', 'ethereum', 'litecoin'],
  }),
  watch: {
    current(v) {
      this.$root.$emit(
        'change-currency',
        {
          bitcoin: 'BTC',
          ethereum: 'ETH',
          litecoin: 'LTC',
        }[v],
      )
    },
  },
}
</script>

<style lang="scss" scoped>
@import '../../../scss/smart-grid';
@import '../../../scss/variables';

@keyframes flip {
  from {
    transform: rotateY(0deg);
  }

  to {
    transform: rotateY(180deg);
  }
}

.currency-selector {
  display: flex;
  height: 100%;
  align-items: center;

  &_item {
    margin-right: 10px;
    width: 50px;
    height: 50px;
    box-shadow: inset 0 0px 10px $cyan-alpha;
    border-radius: 5px;
    background: #0075ff22;
    border: 1px solid $cyan-alpha;
    cursor: pointer;
    transform: scale(0.8);
    transition: all 0.3s;

    &:hover {
      border: 1px solid $cyan;
      box-shadow: inset 0 0px 10px $cyan;

      .currency__icon {
        animation: flip 0.3s ease-in-out infinite;
      }
    }

    .currency__icon {
      width: 100%;
      height: 100%;
      opacity: 0.4;
      transition: all 0.3s;
    }

    &.active {
      box-shadow: inset 0 0px 10px $cyan;
      border: 1px solid $cyan;
      transform: none;

      .currency__icon {
        opacity: 1;
        animation: none;
        transform: rotateY(0deg);
      }
    }
  }
}
</style>
